:root {
  --black: #000000;
  --white: #ffffff;
  --blue: #293449;
  --red: #ee3643;
  --dark-red: rgba(0, 0, 0, 0.24);
  --green: #63a63e;
  --light-blue: #f1f5f7;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/PTSans-Regular.woff2") format("woff2"), url("../../fonts/PTSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/PTSans-Bold.woff2") format("woff2"), url("../../fonts/PTSans-Bold.woff") format("woff");
}
@font-face {
  font-family: "Cuprum";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Cuprum-Regular.woff2") format("woff2"), url("../../fonts/Cuprum-Regular.woff") format("woff");
}
@font-face {
  font-family: "Cuprum";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/Cuprum-Bold.woff2") format("woff2"), url("../../fonts/Cuprum-Bold.woff") format("woff");
}
.body {
  position: relative;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 13px;
  line-height: 24px;
  font-weight: 400;
  background-color: var(--white);
  color: var(--black);
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.container {
  margin: 0 auto;
  width: 940px;
}
.header {
  font-family: "Cuprum", "Arial", sans-serif;
}
.header__search-box-wrapper {
  width: 100%;
  min-height: 42px;
  background: var(--blue);
}
.header__search-box {
  position: relative;
  display: flex;
  justify-content: flex-end;
  min-height: 42px;
}
.header__logo-wrapper {
  position: absolute;
  left: 0;
  padding: 11px 56px 10px;
  background-color: var(--red);
  box-shadow: inset 0 -5px 0 var(--dark-red);
}
.header__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 684px;
}
.header__logo {
  fill: var(--white);
}
.header__link {
  padding: 9px 16px 9px 19px;
  font-size: 18px;
  color: var(--white);
}
.header__link--first {
  position: relative;
  margin-right: 27px;
  margin-left: 32px;
}
.header__link--first::before {
  content: "";
  position: absolute;
  top: 12px;
  left: -3px;
  width: 14px;
  height: 16px;
  background-image: url("../../img/svg/icon-bookmark.svg");
  background-repeat: no-repeat;
}
.header__link--second {
  position: relative;
  margin-right: 11px;
  padding-left: 31px;
}
.header__link--second::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 8px;
  width: 15px;
  height: 15px;
  background-image: url("../../img/svg/icon-cart.svg");
  background-repeat: no-repeat;
}
.header__link--third {
  background-color: var(--green);
}
.header__search {
  position: relative;
  height: 100%;
  font-size: 18px;
  background-color: transparent;
  color: var(--white);
  border: none;
  cursor: pointer;
}
.header__search::before {
  content: "";
  position: absolute;
  top: 12px;
  left: -19px;
  width: 17px;
  height: 17px;
  background-image: url("../../img/svg/icon-search.svg");
  background-repeat: no-repeat;
}
.header__link-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header__contacts {
  padding-top: 46px;
  background-color: var(--light-blue);
}
.header__contacts-list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.header__contacts-item:nth-of-type(1) {
  box-sizing: border-box;
  width: 257px;
  padding-right: 30px;
  padding-left: 30px;
  text-align: center;
}
.header__text {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 23px;
  text-align: left;
  font-style: italic;
  color: var(--red);
}
.header__contacts-item:nth-of-type(2) {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 270px;
}
.header__phone {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  text-align: center;
  font-size: 21px;
  line-height: 30px;
  font-weight: 700;
  color: var(--red);
  background-color: transparent;
  border: 3px solid var(--white);
}
.header__phone::before {
  content: "";
  position: absolute;
  top: 12px;
  left: -3px;
  width: 19px;
  height: 19px;
  background-image: url("../../img/svg/icon-phone.svg");
  background-repeat: no-repeat;
}
.header__contacts-item:nth-of-type(3) {
  width: 281px;
}
